.plan {
    background-image: url("../files/plan_2-01.jpg");
    margin: 0;
    position:fixed;
    height: 100%;
    width: 100%;
    -moz-background-size: cover;
      -webkit-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
    top: 0;
    left: 0; 
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
  }
  .info {
    margin: 0;
    position:fixed;
    height: 100%;
    width: 100%;
    -moz-background-size: cover;
      -webkit-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
    top: 0;
    left: 0; 
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
    transition-duration: .8s;
    opacity: 0;
  }
  
  .infoButton {
    border: dotted rgba(0, 0, 0, 0);
    position: absolute;
    width: 70px;
    height: 70px;
    background-color: rgba(255, 0, 0, 0);
    cursor: pointer;
    
  }

  .pano {
      position: relative;
       height: 100px;
  }
  
  
  
  